@import 'variables';
@import 'fonts';


.carousel {
  .carousel-inner {
    height: 600px;
    overflow: hidden;
  }

  .carousel-item .detail-block {
    position: absolute;
    padding: 2rem;
    top: 10%;
    right: 15%;
    background-color: #fff;
    width: 35rem;
    max-width: 80%;
  }
}

.owl-carousel {
  .owl-stage-outer {
    height: 600px;
  }
  .slide {
    .detail-block {
      position: absolute;
      padding: 2rem;
      top: 10%;
      right: 15%;
      background-color: #fff;
      width: 35rem;
      max-width: 80%;
    }
  }
}